import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-finish-window',
  templateUrl: './finish-window.component.html',
  styleUrls: ['./finish-window.component.scss'],
})
export class FinishWindowComponent implements OnInit, OnDestroy {
  @Input() levelFinishedEmitter: Observable<void>;
  @Input() finishTime: string;
  @Output() startNewLevel = new EventEmitter();
  public isModalDisplayed = false;
  private ngUnsubscribe$ = new Subject();
  constructor() {}

  ngOnInit() {
    this.levelFinishedEmitter.pipe(takeUntil(this.ngUnsubscribe$)).subscribe(() => {
      this.isModalDisplayed = true;
    });
  }

  ngOnDestroy() {
    this.ngUnsubscribe$.next();
    this.ngUnsubscribe$.complete();
  }

  public restartLevel() {
    this.startNewLevel.emit(true);
    this.isModalDisplayed = false;
  }
}
